sourcesContent":["\r\n@use '@/css/scss/_variables.scss' as *;\r\n\r\n.wrapper {\r\n  display: flex !important;\r\n  flex-direction: column;\r\n  justify-content: start;\r\n  align-items: center;\r\n  box-sizing: border-box;\r\n  padding: 5%;\r\n  height: 100%;\r\n  width: 100%;\r\n}\r\n\r\n.address-wrapper {\r\n  height: 30%;\r\n  width: 100%;\r\n  border: 1px solid $txt-success;\r\n  border-radius: 5px;\r\n}\r\n\r\n.text-container {\r\n  display: flex;\r\n  align-items: center;\r\n  margin-bottom: 10px;\r\n}\r\n.text-container-bg {\r\n  animation: shake 2s ease infinite normal forwards;\r\n}\r\n\r\n.drag-handle {\r\n  cursor: grab;\r\n  margin-right: 10px;\r\n}\r\n\r\n.text-box {\r\n  width: 100%;\r\n  padding: 10px;\r\n  border: 1px solid $txt-success;\r\n  border-radius: 5px;\r\n}\r\n\r\n.simple-list {\r\n  list-style-type: none;\r\n  padding: 0;\r\n  \r\n.dragging{\r\n  color: $txt-success;\r\n}\r\n\r\n@keyframes bounce {\r\n\t0% {\r\n\t\tanimation-timing-function: ease-in;\r\n\t\topacity: 1;\r\n\t\ttransform: translateY(-45px);\r\n\t}\r\n\r\n\t24% {\r\n\t\topacity: 1;\r\n\t}\r\n\r\n\t40% {\r\n\t\tanimation-timing-function: ease-in;\r\n\t\ttransform: translateY(-24px);\r\n\t}\r\n\r\n\t65% {\r\n\t\tanimation-timing-function: ease-in;\r\n\t\ttransform: translateY(-12px);\r\n\t}\r\n\r\n\t82% {\r\n\t\tanimation-timing-function: ease-in;\r\n\t\ttransform: translateY(-6px);\r\n\t}\r\n\r\n\t93% {\r\n\t\tanimation-timing-function: ease-in;\r\n\t\ttransform: translateY(-4px);\r\n\t}\r\n\r\n\t25%,\r\n\t55%,\r\n\t75%,\r\n\t87% {\r\n\t\tanimation-timing-function: ease-out;\r\n\t\ttransform: translateY(0px);\r\n\t}\r\n\r\n\t100% {\r\n\t\tanimation-timing-function: ease-out;\r\n\t\topacity: 1;\r\n\t\ttransform: translateY(0px);\r\n\t}\r\n}\r\n}\r\n","// Font Variables\r\n$font-primary: 'Noto Sans', sans-serif;\r\n\r\n// Font Size Variables\r\n$font-size-xs: 12px;\r\n$font-size-sm: 14px;\r\n$font-size-md: 16px;\r\n$font-size-lg: 18px;\r\n$font-size-xl: 20px;\r\n$font-size-xxl: 24px;\r\n\r\n\r\n\r\n// Font Weight Variables\r\n$font-weight-light: 300;\r\n$font-weight-regular: 400;\r\n$font-weight-medium: 500;\r\n$font-weight-bold: 700;\r\n\r\n/* Colors */\r\n\r\n//Background Color\r\n$bg-primary:  #f16622;\r\n$bg-light: #FFFFFF;\r\n\r\n//Background Color State\r\n$bg-success: #DBFFE3;\r\n$bg-error: #FFEEEF;\r\n\r\n\r\n// Header Color\r\n$header: #4C4C4C;\r\n$header-active: #f16622;\r\n\r\n// Text Color\r\n$txt-primary: #4C4C4C;\r\n$txt-secondary: #232323;\r\n\r\n// Text Color State\r\n$txt-success: #05B32B;\r\n$txt-error: #CB3F49;\r\n$txt-awaiting: #5578B1;\r\n$txt-in-progress: #FF7937;\r\n$txt-processing: #4C4C4C;\r\n$txt-picked: #005353;\r\n$txt-expired: #232323;\r\n\r\n// Background Color State\r\n$bg-success: #DBFFE3;\r\n$bg-error: #FFEEEF;\r\n$bg-awaiting: #E0F0FD;\r\n$bg-in-progress: #FFEEEF;\r\n$bg-processing: #FEF9E1;\r\n$bg-picked: #93DFDF;\r\n$bg-expired: #D9D9D9;\r\n\r\n// Border Color  \r\n$border-color: #EDEDED;\r\n\r\n"],"sourceRoot":""}]);
